import Button from 'components/Button'
import { Flexbox, Page } from 'components'
import { PRODUCTS, HOME } from 'constants/routes'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import { Redirect, useHistory, useLocation } from 'react-router-dom'
import { CheckCircleOutlined } from '@ant-design/icons'
import { checkoutContext } from 'providers/CheckoutProvider'
import { useDocumentTitle, useScrollTop } from 'hooks'
import { StepTitle, Wrapper } from '../common'
import { CheckoutResultLocationState } from '../CheckoutPayment/useForm'

const CheckoutResult = () => {
  const { state } = useLocation<CheckoutResultLocationState>()

  const { t } = useTranslation('checkout')

  useDocumentTitle(t('checkoutResult.tabTitle'))
  useScrollTop()

  const history = useHistory()
  const { checkout } = useContext(checkoutContext)

  if (!state?.orderID) {
    return <Redirect to={HOME} />
  }

  return (
    <Page>
      <Wrapper>
        <StepTitle>{t('checkoutResult.title')}</StepTitle>

        <Flexbox marginTop="3rem" justifyContent="center">
          <CheckCircleOutlined style={{ fontSize: '8rem' }} />
        </Flexbox>

        <Flexbox
          marginTop="3rem"
          alignItems="center"
          flexDirection="column"
          justifyContent="center"
        >
          <p style={{ marginBottom: 0 }}>{t('checkoutResult.orderId')}</p>
          <h3>{state.orderID}</h3>
        </Flexbox>

        <Flexbox
          marginTop="5rem"
          flexDirection="row-reverse"
          justifyContent="space-between"
        >
          <Button variant="primary" onClick={() => history.push(PRODUCTS)}>
            {t('checkoutResult.actions.backToShop')}
          </Button>
        </Flexbox>
      </Wrapper>
    </Page>
  )
}

export default CheckoutResult
