import Flexbox from 'components/Flexbox'
import React, { ReactNode } from 'react'
import { FlexDirectionProps, SpaceProps } from 'styled-system'

type Props = {
  children?: ReactNode
} & SpaceProps &
  FlexDirectionProps

const Tile = ({ children, ...props }: Props) => (
  <Flexbox
    width="100%"
    padding="30px"
    background="white"
    border="1px solid"
    borderColor="border-color"
    flexDirection="column"
    {...props}
  >
    {children}
  </Flexbox>
)

export default Tile
