/* eslint-disable react/forbid-prop-types */
import { FieldProps, useField } from 'formik'
import { Mobile } from 'models/order'
import { FormValues } from 'pages/checkout/CheckoutDetails/useForm'
import React from 'react'
import PhoneInput from 'react-phone-input-2'
import styled from 'styled-components'

type Props = {
  disabled?: boolean
  defaultValue?: Mobile
  placeholder?: string
  label: string
  fieldProps: FieldProps<Mobile, FormValues>
  name: string
}

const CustomMobileInput = ({
  placeholder,
  fieldProps,
  defaultValue,
  label,
  disabled,
  name
}: Props) => {
  const [field, meta, helpers] = useField(name)
  const { touched, error } = meta
  const { setValue } = helpers

  const handleChange = (value, data) => {
    const mob = {
      dialCode: data.dialCode,
      countryCode: data.countryCode,
      country: data.name,
      value
    }

    setValue(mob)
  }

  return (
    <div className="input-group">
      {touched && error ? (
        <span className="label-input label-error">
          {error?.value || error?.dialCode}
        </span>
      ) : (
        <label className="label-input" htmlFor={field.name}>
          {label}
        </label>
      )}

      <StyledPhoneInput
        isError={!!touched && !!error}
        disabled={disabled}
        country="pl"
        inputClass="input-form d-block"
        inputExtraProps={{ required: true }}
        onChange={handleChange}
        placeholder={placeholder}
        disableDropdown
        countryCodeEditable={false}
        value={defaultValue.value}
        masks={{ pl: '... .. .. ..' }}
      />
    </div>
  )
}

type StyledPhoneInputProps = {
  isError?: boolean
}

const StyledPhoneInput = styled(PhoneInput)<StyledPhoneInputProps>`
  border: ${(props) => (props.isError ? '1px solid red' : '1px solid blue')};
`

export default CustomMobileInput
