import 'normalize.css/normalize.css'
import AuthProvider from 'old/providers/authProvider'
import AuthStatusProvider from 'old/providers/authStatusProvider'
import {
  BasketProvider,
  BasketToggleProvider,
  CheckoutProvider,
  LoaderProvider,
  RemoteConfigProvider
} from 'providers'
import ProfileProvider from 'old/providers/profileProvider'
import React, { StrictMode } from 'react'
import { render } from 'react-dom'
import 'react-phone-input-2/lib/style.css'
import { QueryClient, QueryClientProvider } from 'react-query'
import 'old/styles/style.scss'
import WebFont from 'webfontloader'
import { ThemeProvider } from 'styled-components'
import theme from 'styles/theme'
import { I18nextProvider } from 'react-i18next'
import i18n from 'i18n/config'

import { App } from './pages'

WebFont.load({
  google: {
    families: ['Roboto']
  }
})

const root = document.getElementById('app')

export const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
      staleTime: 90000
    }
  }
})

render(
  <StrictMode>
    <AuthStatusProvider>
      <AuthProvider>
        <ProfileProvider>
          <BasketProvider>
            <QueryClientProvider client={queryClient}>
              <ThemeProvider theme={theme}>
                <I18nextProvider i18n={i18n}>
                  <CheckoutProvider>
                    <BasketToggleProvider>
                      <RemoteConfigProvider>
                        <LoaderProvider>
                          <App />
                        </LoaderProvider>
                      </RemoteConfigProvider>
                    </BasketToggleProvider>
                  </CheckoutProvider>
                </I18nextProvider>
              </ThemeProvider>
            </QueryClientProvider>
          </BasketProvider>
        </ProfileProvider>
      </AuthProvider>
    </AuthStatusProvider>
  </StrictMode>,
  root
)
